import React, { useState } from 'react';
import db from 'libs/db';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { toast } from 'react-toastify';
import AdminServices from 'services/api/AdminServices';

export default function ElssReport(props) {
    const [clientData, setClientData] = useState({});
    const [loading, setLoading] = useState();
    const [year,setYear] = useState();
    const data = [
        { key: '0', name: 'FY 2021-2022' },
        { key: '1', name: 'FY 2022-2023' }
    ]

    React.useEffect(() => {
        setLoading(true)
        try {
            db.get(db.model.user).then(async (user) => {
                await getClientData(user.userData.id)
                setLoading(false)
            })
        } catch (error) {
            toast.error(error);
            setLoading(false)
        }
    }, [])

    const getClientData = async (id) => {
        await db.get(db.model.clientData).then(async (p) => {
            if (p === null) {
                let res = await AdminServices.getClient(id).then(res => res);
                if (res.type === true) {
                    const a = JSON.parse(res.data);
                    let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
                    console.log("Get Client API :", aa)
                    setClientData(aa)
                    await db.set(db.model.clientData, JSON.stringify(aa))
                }
            } else {
                setClientData(p)
            }
        })
    }

    return (
        <div className='mainContainer'>
            <MainHeader header='ELSS report' right='Menu' rightAction={() => { props.history.push('/menu') }} navigation={props.history} />
            <div className='dataContainer'>
                <div className='bigHeader' style={{marginTop:"12%"}}>Select a financial year to download</div>
                {data.map((obj) => (
                    <div key={obj.key} style={{ display: 'flex', marginTop: "6%" }}>
                        <div className="radio-item">
                            <input
                                className='normalInput'
                                type="radio"
                                id={obj.key}
                                name="ritem"
                                value={obj.key}
                                onChange={(val) => {
                                    setYear(val.target.value)
                                }}
                                checked={year === obj.key}
                            />
                            <label className="bodyTextDark radio-label" htmlFor={obj.key}>{obj.name}</label>
                        </div>
                    </div>
                ))}
            </div>
            <div className='login-footer'>
                <button className='solidButton'>
                    Download ELSS report
                </button>
            </div>
        </div>
    )
}
